export default {
  data () {
    return {
      name: ''
    }
  },
  mounted () {
    const $this = this;
    $this.name = $this.$route.query.userName;
    setTimeout(function () {
      $this.$vux.toast.show({
        text: '验票成功',
        width: '2.3rem',
        position: 'middle',
        time: 2000
      })
    },500)
  }
}



